import { writable } from 'svelte/store';
import { initialCanvas } from '../utils/initial-canvas';
const canvas = () => {
    const { subscribe, set, update } = writable(initialCanvas());
    let currentBlobs;
    subscribe(c => currentBlobs = c.blobs);
    const getBlobId = (index) => currentBlobs[index].id;
    return {
        subscribe,
        set,
        update,
        getBlobId,
    };
};
export const canvasState = canvas();
